import {projectMapInit} from '../../components/project-map';

export function barbaEnterProject(data) {

    setTimeout(()=>{
        projectMapInit();

        const $projectMap = $('#project-map');
        const offset = $projectMap.offset().top;
        const diff = $('body').innerHeight() - $('.site-footer').innerHeight();
        const diff2 = diff - $(window).innerHeight();

        const equalWindow = $(window).innerHeight() !== diff - offset;


        if ($(window).width() > 960) {
            $(window).scroll(()=> {

                const scrollTop = $(window).scrollTop();

                if(offset < scrollTop && diff2 > scrollTop && equalWindow) {
                    $projectMap.parent().removeClass('abs').addClass('sticky').removeAttr('style');
                } else if(diff2 < scrollTop && equalWindow) {
                    $projectMap.parent().removeClass('sticky').addClass('abs').css('top', diff2 - offset);
                } else {
                    $projectMap.parent().removeClass('sticky').removeClass('abs').removeAttr('style');
                }
            });
        }
    }, 1000);




}
