import '../scss/app.scss';
import $ from 'jquery';
import barba from '@barba/core';
import {
    barbaEnter,
    barbaEnterConstruction,
    barbaEnterContact,
    barbaEnterHome,
    barbaEnterProject
} from './modules/barba-enter';
import './components/site-navigation';
import './components/popup';
import {isMobile} from "mobile-device-detect";

$(document).ready(init);

function init() {
    barba.init({
        debug: false,

        /*prevent: (el) => {
            return $(el.el).is('.fancybox') || $(el.el).is('[data-fancybox]');
        },*/

        /*
        * Home page views
        * */

        views: [
            {
                namespace: 'home',
                beforeEnter(data) {
                    barbaEnter(data);
                },
                afterEnter(data) {
                    barbaEnterHome(data);
                },
                afterLeave() {
                    $('html').removeClass('overflow-hidden');
                }
            },
            {
                namespace: 'about',
                beforeEnter(data) {
                    barbaEnter(data);
                }
            },
            {
                namespace: 'construction',
                beforeEnter(data) {
                    barbaEnter(data);
                },
                afterEnter(data) {
                    barbaEnterConstruction(data);
                }
            },
            {
                namespace: 'project',
                beforeEnter(data) {
                    barbaEnter(data);
                },
                afterEnter(data) {
                    barbaEnterProject(data);
                }
            },
            {
                namespace: 'contact',
                beforeEnter(data) {
                    barbaEnter(data);
                },
                afterEnter(data) {
                    barbaEnterContact(data);
                }
            }
        ],

        /*
        * Page transitions
        * */

        transitions: [
            /*{
                name: 'to-project',
                to: {
                    namespace: [
                        'project'
                    ]
                },
                after: barbaEnterProject
            }*/
        ]
    });

    // $('.project-page').length ? barbaEnterProject('') : null;

    if (isMobile) {
        $('html').addClass('mobile');
    }
}
