const $siteNavigation = $('.site-navigation');
const $siteHeaderBurger = $('.site-header-burger');

$siteNavigation.find('>ul>li').hover(function () {
    const $subNavigation = $(this).find('.sub-navigation');

    if($subNavigation.length) {
        $siteNavigation.addClass('show-sub-navigation');
    }
}, ()=> {
    $siteNavigation.removeClass('show-sub-navigation');
});

$siteHeaderBurger.click(function () {
    $('body').toggleClass('show-mobile-navigation');
});
