import 'jquery-mousewheel';
import {TimelineMax} from 'gsap';

export function barbaEnterHome(data) {
    const $h = $('html');

    $h.addClass('overflow-hidden home-preview-active');

    let scrollCheck = false;
    let timeLine = new TimelineMax();

    const opacityObjects = ['.site-header', '.home-content'];

    timeLine.set(opacityObjects, {opacity: 0});

    $('.home-page').on('mousewheel touchstart', function () {
        if (!scrollCheck) {
            scrollCheck = true;

            $h.removeClass('home-preview-active');

            timeLine.to(
                '.home-preview',
                .7,
                {
                    opacity: 0,
                }
            ).set(
                '.home-preview',
                {
                    display: 'none'
                }
            ).to(
                opacityObjects,
                1,
                {
                    opacity: 1
                }
            );
        }
    });
}
