export function initFormSender() {

    const $form = $('.contact-form');

    if(!$form.length) {
        return;
    }

    $form.on('submit', function(e){
      e.preventDefault();

      $.ajax({
        type: 'POST',
        url: $form.attr('action'),
        data: $form.serialize(),
        dataType: 'json',
        success: function(response) {
          if(response['success']){
            $form.find('button').text('Thanks!');
            $form.find('button').attr('disabled', '');
          }
        },
      });
    })
}
