import {mapStyles} from './map-styles';

export function constructionMapInit() {
    let $constructionMap = $('#construction-map');

    if(!$constructionMap.length) {
        return;
    }

    const $constructionMapPopups = $(".construction-map-popups");

    let mapContainer = document.getElementById('construction-map');

    let map = new google.maps.Map(mapContainer, {
        zoom: parseInt(mapContainer.dataset.zoom),
        center: {lat: parseFloat(mapContainer.dataset.lat), lng: parseFloat(mapContainer.dataset.lng)},
        disableDefaultUI: true,
        styles: mapStyles
    });

    // draw markers
    let posMarkers = {};
    let infoWindows = {};
    let clickMarkers = {};

    const icon = '/assets/img/portfolio/point.png';

    // init markers
    let mapMarkers = {};

    $.getJSON('/assets/json/construction-map.json', function(data) {
        mapMarkers = data["data"];

        const hash = window.location.hash;
        const $tabNavigationItem = $('.tab-navigation-item');
        const $tabPane = $('.tab-pane');
        const $projectItem = $('.project-list-item');

        let Popup = createPopupClass();

        for (let marker in mapMarkers) {

            const currentMarker = mapMarkers[marker];

            posMarkers[marker] = new google.maps.Marker({
                position: new google.maps.LatLng(currentMarker.lat, currentMarker.lng),
                map: map,
                icon: icon,
                visible: currentMarker.visible,
                anchorPoint: new google.maps.Point(20, 20)
            });

            const div = $('<div id="construction-map-info-popup-'+ marker +'">' +
                '<div class="empty-icon"><img src="/assets/img/portfolio/point-empty.png" alt=""></div>' +
                '<div class="text">'+ currentMarker.content +'</div>' +
                '</div>');

            $constructionMapPopups.append(div);

            infoWindows[marker] = new Popup(
                new google.maps.LatLng(currentMarker.lat, currentMarker.lng),
                document.getElementById("construction-map-info-popup-" + marker)
            );

            infoWindows[marker].setMap(map);

            clickMarkers[marker] = (function (marker) {
                return function(e) {
                    map.panTo(posMarkers[marker].getPosition());

                    for (let marker in mapMarkers) {
                        posMarkers[marker].setOpacity(1);
                    }

                    posMarkers[marker].setOpacity(0);

                    $projectItem.removeClass('active');
                    $('.project-list-item[data-point="'+ currentMarker.id +'"]').addClass('active');

                    $("[id^=construction-map-info-popup-]").parent().hide();
                    $("#construction-map-info-popup-" + marker).parent().show();
                }
            })(marker);

            posMarkers[marker].addListener('click', clickMarkers[marker]);
            posMarkers[marker].setVisible(false);
        }

        if(hash) {
            $tabNavigationItem.removeClass('active');
            $tabPane.removeClass('active');
            $('[data-tab="'+ hash +'"]').addClass('active');
            $(hash).addClass('active');

            showMarkerFromTypes(hash.substring(1));
        } else {
            showMarkerFromTypes('dev');
        }

        let $constructionMap = $('#construction-map');
        let offset = $constructionMap.offset().top;
        let diff = $('body').innerHeight() - $('.site-footer').innerHeight();
        let diff2 = diff - $(window).innerHeight();

        let equalWindow = $(window).innerHeight() !== diff - offset;

        $(window).scroll(()=> {

            const scrollTop = $(window).scrollTop();

            if(offset < scrollTop && diff2 > scrollTop && equalWindow) {
                $constructionMap.parent().removeClass('abs').addClass('sticky').removeAttr('style');
            } else if(diff2 < scrollTop && equalWindow) {
                $constructionMap.parent().removeClass('sticky').addClass('abs').css('top', diff2 - offset);
            } else {
                $constructionMap.parent().removeClass('sticky').removeClass('abs').removeAttr('style');
            }
        });

        $tabNavigationItem.click(function (e) {
            e.preventDefault();

            const tab = $(this).data('tab');

            $tabNavigationItem.removeClass('active');
            $tabPane.removeClass('active');
            $(tab).addClass('active');
            $(this).addClass('active');

            history.pushState(null, null, tab);
            // window.location.hash = tab;

            $constructionMap = $('#construction-map');
            offset = $constructionMap.offset().top;
            diff = $('body').innerHeight() - $('.site-footer').innerHeight();
            diff2 = diff - $(window).innerHeight();

            equalWindow = $(window).innerHeight() !== diff - offset;

            showMarkerFromTypes(tab.substring(1));
        });

        $projectItem.hover(function () {
           const point = $(this).data('point');
           let index = undefined;
            $projectItem.removeClass('active');

            for (let marker in mapMarkers) {

                if(mapMarkers[marker].id === point){
                    index = marker;
                    map.panTo(posMarkers[marker].getPosition());

                    for (let marker in mapMarkers) {
                        posMarkers[marker].setOpacity(1);
                    }

                    posMarkers[marker].setOpacity(0);
                } else {
                    posMarkers[marker].setVisible(false);
                }
            }

            $("[id^=construction-map-info-popup-]").parent().hide();
            $("#construction-map-info-popup-" + index).parent().show();
        }, function () {
            const type = $('.tab-pane.active').attr('id');
            showMarkerFromTypes(type);
        });


        function showMarkerFromTypes(type) {
            for (let marker in mapMarkers) {
                posMarkers[marker].setOpacity(1);

                if(type === mapMarkers[marker].type) {
                    posMarkers[marker].setVisible(true);

                    $("#construction-map-info-popup-" + marker).parent().hide();
                }
                else {
                    posMarkers[marker].setVisible(false);

                    // close all info windows
                    $("#construction-map-info-popup-" + marker).parent().hide();
                }

            }
        }
    });
}
/**
 * Returns the Popup class.
 *
 * Unfortunately, the Popup class can only be defined after
 * google.maps.OverlayView is defined, when the Maps API is loaded.
 * This function should be called by initMap.
 */
function createPopupClass() {
    /**
     * A customized popup on the map.
     * @param {!google.maps.LatLng} position
     * @param {!Element} content The bubble div.
     * @constructor
     * @extends {google.maps.OverlayView}
     */
    function Popup(position, content) {
        this.position = position;

        content.classList.add('construction-map-info-popup-container');

        // // This zero-height div is positioned at the bottom of the bubble.
        /*let closeButton = document.createElement('div');
        closeButton.classList.add('close');
        content.appendChild(closeButton);

        closeButton.addEventListener("click", function() {
            this.parentNode.parentNode.style.display = "none";
        });*/

        // This zero-height div is positioned at the bottom of the tip.
        this.containerDiv = document.createElement('div');
        this.containerDiv.classList.add('construction-map-info-popup');
        this.containerDiv.appendChild(content);

        // Optionally stop clicks, etc., from bubbling up to the map.
        google.maps.OverlayView.preventMapHitsAndGesturesFrom(this.containerDiv);
    }

    // ES5 magic to extend google.maps.OverlayView.
    Popup.prototype = Object.create(google.maps.OverlayView.prototype);

    /** Called when the popup is added to the map. */
    Popup.prototype.onAdd = function() {
        this.getPanes().floatPane.appendChild(this.containerDiv);
    };

    /** Called when the popup is removed from the map. */
    Popup.prototype.onRemove = function() {
        if (this.containerDiv.parentElement) {
            this.containerDiv.parentElement.removeChild(this.containerDiv);
        }
    };

    /** Called each frame when the popup needs to draw itself. */
    Popup.prototype.draw = function() {
        let divPosition = this.getProjection().fromLatLngToDivPixel(this.position);

        // Hide the popup when it is far out of view.
        let display =
            Math.abs(divPosition.x) < 4000 && Math.abs(divPosition.y) < 4000 ?
                'block' :
                'none';

        if (display === 'block') {
            this.containerDiv.style.left = divPosition.x + 'px';
            this.containerDiv.style.top = divPosition.y + 'px';
        }
        // glebsn - enter display property by click event
        // if (this.containerDiv.style.display !== display) {
        //   this.containerDiv.style.display = display;
        // }
    };

    return Popup;
}
