import {initContactMap} from '../../components/contact-map';
import {initFormSender} from '../../components/contact-form';

export function barbaEnterContact(data) {
    const $formControl = $(".form-control");

    $formControl.blur(function() {
        if ($(this).next('.label').length) {
            if ($(this).val()) {
                $(this).next('.label').addClass("filled");
            } else {
                $(this).next('.label').removeClass("filled");
            }
        }
    });

    initContactMap();

    initFormSender();
}
