$(document).on('click', '.open-popup', function (e) {
    e.preventDefault();

    $('#' + $(this).data('popup')).addClass('show');
});

$(document).on('click', '.close-popup', function (e) {
    e.preventDefault();

    $(this).parents('.popup').addClass('hide');

    setTimeout(()=>{
        $(this).parents('.popup').removeClass('show hide');
    }, 700);
});
