export function barbaEnter(data) {
    const $w = $(window);
    const $b = $('body');
    const $siteNavigation = $('.site-navigation');

    $w.scrollTop(0);

    $siteNavigation.find('a.active').removeClass('active');
    $siteNavigation.find('a[href="/' + data.next.url.path.split('/')[1] + '"]').addClass('active');

    $b.is('.show-mobile-navigation') ? $b.removeClass('show-mobile-navigation') : null;
}
