import {mapStyles} from './map-styles';

export function projectMapInit() {
    let $projectMap = $('#project-map');

    if(!$projectMap.length) {
        return;
    }

    let Popup = createPopupClass();

    const $constructionMapPopups = $(".construction-map-popups");

    let mapContainer = document.getElementById('project-map');
    const icon = '/assets/img/portfolio/point.png';

    let map = new google.maps.Map(mapContainer, {
        zoom: parseInt(mapContainer.dataset.zoom),
        center: {lat: parseFloat(mapContainer.dataset.lat), lng: parseFloat(mapContainer.dataset.lng)},
        disableDefaultUI: true,
        styles: mapStyles
    });

    let marker = new google.maps.Marker({
        position: new google.maps.LatLng(parseFloat(mapContainer.dataset.lat), parseFloat(mapContainer.dataset.lng)),
        map: map,
        icon: icon,
        visible: true,
        anchorPoint: new google.maps.Point(20, 20)
    });

    const div = $('<div id="project-map-info-popup">' +
        '<div class="empty-icon"><img src="/assets/img/portfolio/point-empty.png" alt=""></div>' +
        '<div class="text">'+ mapContainer.dataset.content +'</div>' +
        '</div>');

    $constructionMapPopups.append(div);

    let infoWindow =new Popup(
        new google.maps.LatLng(parseFloat(mapContainer.dataset.lat), parseFloat(mapContainer.dataset.lng)),
        document.getElementById("project-map-info-popup")
    );

    infoWindow.setMap(map);

    marker.setOpacity(0);

    setTimeout(()=> {
        $("#project-map-info-popup").parent().show();
    }, 300);
}
/**
 * Returns the Popup class.
 *
 * Unfortunately, the Popup class can only be defined after
 * google.maps.OverlayView is defined, when the Maps API is loaded.
 * This function should be called by initMap.
 */
function createPopupClass() {
    /**
     * A customized popup on the map.
     * @param {!google.maps.LatLng} position
     * @param {!Element} content The bubble div.
     * @constructor
     * @extends {google.maps.OverlayView}
     */
    function Popup(position, content) {
        this.position = position;

        content.classList.add('construction-map-info-popup-container');

        // // This zero-height div is positioned at the bottom of the bubble.
        /*let closeButton = document.createElement('div');
        closeButton.classList.add('close');
        content.appendChild(closeButton);

        closeButton.addEventListener("click", function() {
            this.parentNode.parentNode.style.display = "none";
        });*/

        // This zero-height div is positioned at the bottom of the tip.
        this.containerDiv = document.createElement('div');
        this.containerDiv.classList.add('construction-map-info-popup');
        this.containerDiv.appendChild(content);

        // Optionally stop clicks, etc., from bubbling up to the map.
        google.maps.OverlayView.preventMapHitsAndGesturesFrom(this.containerDiv);
    }

    // ES5 magic to extend google.maps.OverlayView.
    Popup.prototype = Object.create(google.maps.OverlayView.prototype);

    /** Called when the popup is added to the map. */
    Popup.prototype.onAdd = function() {
        this.getPanes().floatPane.appendChild(this.containerDiv);
    };

    /** Called when the popup is removed from the map. */
    Popup.prototype.onRemove = function() {
        if (this.containerDiv.parentElement) {
            this.containerDiv.parentElement.removeChild(this.containerDiv);
        }
    };

    /** Called each frame when the popup needs to draw itself. */
    Popup.prototype.draw = function() {
        let divPosition = this.getProjection().fromLatLngToDivPixel(this.position);

        // Hide the popup when it is far out of view.
        let display =
            Math.abs(divPosition.x) < 4000 && Math.abs(divPosition.y) < 4000 ?
                'block' :
                'none';

        if (display === 'block') {
            this.containerDiv.style.left = divPosition.x + 'px';
            this.containerDiv.style.top = divPosition.y + 'px';
        }
        // glebsn - enter display property by click event
        // if (this.containerDiv.style.display !== display) {
        //   this.containerDiv.style.display = display;
        // }
    };

    return Popup;
}
